$bgColorNavBar: #3C4554;
$colorTextNavBar: #E3E2DD;
$activeTextNavBar: #87A6C0;
$bgColorPage: #E3E2DD;
$colorAllText: #1C1B22;
$colorButton: #da6767;

body {
  background-color: $bgColorPage;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

* {
  color: $colorAllText;
}

button {
  background-color: $bgColorPage;
  border: 1px solid $colorButton;
  border-radius: 4px;
  font-size: 14px;
  color: $colorButton;
  text-transform: uppercase;
  padding: 10.5px 15px;
  transition: 0.3s ease;

  &:hover {
    background-color: $colorButton;
    color: $bgColorPage;
    cursor: pointer;
  }
}

.container {
  margin: 15px auto;
  padding: 0 10px;
  max-width: 1200px;
}
