@import "src/style/base";

// Основные стили для навигационного бара
.navbar {
  top: 0;
  left: 0;
  right: 0;
  padding: 0 40px;
  background-color: $bgColorNavBar;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.3);

  // Стили контейнера для навигационного бара
  .container {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1350px;
    height: 100%;
  }
}

// Стили для текста в левом углу
.navbar__brand {
  font-size: 24px;
  font-weight: 400;
  line-height: 1.6;
  margin: 10px 0;
  color: $colorTextNavBar;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

// Стили для кнопки выпадающего меню в правом углу
.navbar__menu_btn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: $colorTextNavBar;
  font-size: 24px;
  font-weight: 400;
  width: 60px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s ease;

  &__icon {
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid $colorTextNavBar;
    margin-right: 5px;
    transition: transform 0.2s ease-in-out;
    transform: rotate(0deg);
  }

  &__icon--open {
    border-top: 8px solid $activeTextNavBar;
    transform: rotate(180deg);
  }

  &:hover {
    color: $activeTextNavBar;
  }
}

// Стили для выпадающего меню
.navbar__menu {
  position: absolute;
  top: 60px;
  z-index: 1;
  padding: 0;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  list-style: none;
  display: none;
  border-radius: 4px;

  &__item {
    margin: 10px 5px;
    white-space: nowrap;
  }

  &__link {
    color:  $colorAllText;
    text-decoration: none;
    font-size: 20px;

    &:hover {
      color: #0077cc;
    }
  }
}

// Стили для выпадающего меню, когда оно открыто
.navbar__menu--open {
  display: block;
}