@import "src/style/base";

.table {
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  font-size: 1.2rem;
  color: $bgColorNavBar;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;

  th,
  td {
    padding: 10px;
    text-align: left;
    vertical-align: middle;
    border: 1px solid #ccc;
  }

  td:last-child {
    text-align: right;
  }

  th {
    background-color: #f5f5f5;
    font-weight: 700;
  }

  tbody tr:nth-child(even) {
    background-color: #f5f5f5;
  }

  tbody tr:hover {
    background-color: $activeTextNavBar;
    cursor: pointer;
  }
}